import React from 'react'
import { BrowserRouter as Router, Link, Switch, Route } from 'react-router-dom'
import Register from './Home'
import Output from './users'
import './style.css'

const Form = () => {
  return (
    <div className='nav-container'>
      <Router>
        <nav className='nav'>
          <ul>
            <li><Link to='/'>Register</Link></li>
            <li><Link to='/users'>User Info</Link></li>
          </ul>
        </nav>
        <Switch>
          <Route path='/users'>
            <Output />
          </Route>
          <Route path='/'>
            <Register />
          </Route>
        </Switch>
      </Router>
    </div>
  )
}
export default Form
