import React, { useState } from 'react'
import './style.css'

const FormAdd = ({ AddUser }) => {
  const [name, setName] = useState(' ')
  const [identity, setIdentity] = useState(' ')
  const [surname, setSurname] = useState(' ')
  const [password, setPassword] = useState(' ')
  const [dept, setDept] = useState(' ')
  const [email, setEmail] = useState(' ')

  const SubmitInfo = (e, alert) => {
    if (Validate()) {
      AddUser(name, surname, email, identity, password, dept)
      e.preventDefault()
    } else {
      if (name.length < 3) {
        alert(name + " " + ' is invalid')
      }
      if (email.length < 3) {
        alert(name + " " + ' is invalid')
      }
      if (password.length < 3) {
        alert(password + " " + ' is not correct')
      }
      if (surname.length < 3) {
        alert(surname + " " + ' is not valid')
      }
    }
  }
  const Validate = (e) => {
    e.preventDefault()
    if (name.length < 3) {
      return false
    }
    if (email.length < 3) {
      return false
    }
    if (surname.length < 3) {
      return false
    }
    if (identity.length < 3) {
      return false
    }
    return true
  }

  return (
    <div className='input'>
      <form onSubmit={SubmitInfo}>
        <fieldset>
          <h2> Registration Form</h2>
          <label>Name</label><input type='text' onChange={(e) => setName(e.target.value)} name='name' placeholder='Enter Name' />
          <br /> <label>Surname</label><input type='text' onChange={(e) => setSurname(e.target.value)} name='surname' placeholder='Enter Surname' />
          <br /> <label>Identity Number</label> <input type='text' onChange={(e) => setIdentity(e.target.value)} name='identity' placeholder='Enter Identity' />
          <br /><label>Password</label> <input type='password' onChange={(e) => setPassword(e.target.value)} name='password' placeholder='Enter Password' />
          <br /> <label>Email Address:</label> <input type='email' onChange={(e) => setEmail(e.target.value)} name='email' placeholder='Enter Email Address' />
          <br /> <label> Your Department:</label><br />
          <select name='name' onChange={(e) => setDept(e.target.value)}>
            <option value=' '>
              Select Your Department
            </option>
            <option value='IT'>
              IT Department
            </option>
            <option value='HR'>
              Human Resources
            </option>
            <option value='PR '>
              Public Relations
            </option>
          </select>
          <br />  <button type='submit'>Get Data</button>
        </fieldset>
      </form>
    </div>
  )
}
export default FormAdd
