import React, { useState } from 'react'
import FormAdd from './form'
import './style.css'
import { Link } from 'react-router-dom'

const Register = () => {
  const [user, setUser] = useState([])

  const AddUser = (name, identity, surname, password, dept, email) => {
    setUser([...user, {
      uid: 1,
      name: name,
      id: identity,
      surname: surname,
      password: password,
      dept: dept,
      email: email

    }])
  }
  return (
    <div className='form-container'>
      <div className='section'>

        <div className='col grid_1_of_3'> </div>

        <div className='col grid_1_of_3'> </div>

        <div className='col grid_1_of_3'> </div>

      </div>
      <div className='form-input'>
        <FormAdd AddUser={AddUser} />
      </div>
      <div className='output'>
        <h1>User List</h1>
        <Link to='/users'>{user.map(action => <li key={action.uid}>{action.name + ' ' + action.surname + ' ' + action.id + ' ' + action.email}</li>)}</Link>

      </div>
    </div>
  )
}
export default Register
